.headerContainer {
  display: flex;
  background-image: url("../assets/header/BRAQ_Social05-04.png");
  background-size: 100%;
  background-repeat: no-repeat;
  justify-content: flex-start;
  flex: 1;
  aspect-ratio: 3.2;
}

.banner {
  max-width: fit-content;
  size: 10%;
}

.emptyContainer {
  width: 45%;
}

.logoContainer {
  width: 10%;
}

.linkContainer {
  width: 45%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.aboutheaderContainer {
  width: 25%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.whitepaperContainer {
  width: 25%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.whitepaper {
  cursor: pointer;
}
.openseaContainer {
  width: 25%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
}

.Discord {
  background-color: #f2d5e7;
  border: none;
  border-radius: 30px;
  color: rgba(110, 82, 110, 0.863);
  padding: 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12.5px;
  cursor: pointer;
}

.DiscordContainer {
  width: 25%;
  padding-top: 30px;
  margin-left: 20px;
  justify-content: center;
  align-items: center;
  color: antiquewhite;
  text-decoration-color: black;
}

.backgroundImage {
  background-image: url("../assets/banner/Braq_Background-03.png");
  background-size: cover;
  background-color: transparent;
  size: 50%;
}
