.aboutContainer {
  width: 100%;
  display: contents;
}

.aboutusContainer {
  justify-content: center;
  align-items: center;
}

.aboutus {
  font-family: "CaviarDreamsBold";
  color: #c6cde8;
  font-family: "CaviarDreamsBold";
  font-size: 40px;
  font-weight: bold;
}

.aboutContent {
  background-color: #beb7db;
  padding: 40px;
  border-radius: 50px;
  font-family: "CaviarDreamsBold";
  color: white;
  margin: 30px;
}

.investContainer {
  justify-content: center;
  align-items: center;
  display: contents;
  margin: 20px;
}

.invest {
  font-family: "CaviarDreamsBold";
  color: #c6cde8;
  font-size: 40px;
  font-weight: bold;
}

.investContent {
  background-color: #c8e9f1;
  padding: 40px;
  border-radius: 50px;
  font-family: "CaviarDreamsBold";
  color: rgba(110, 82, 110, 0.863);
  margin: 30px;
}

.whitepapertext {
  margin: 10px;
}
.whitepapertextContainer {
  display: contents;
  padding: 30px;
  justify-content: center;
  border: solid;
}

.whitepaper {
  text-decoration: #beb7db;
}

.head {
  margin-top: 50px;
  padding: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.headcontainer {
  border-radius: 20px;
  padding: 10px;
  width: fit-content;
  color: #c6cde8;
  font-size: 24px;
  font-family: "CaviarDreamsBold";
}

.teamprofileContainer {
}

.teamContainer {
  width: 100%;
}

.profileContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile {
  margin: 30px;
  padding-bottom: 10px;
  border-color: #f2d5e7;
  height: max-content;
  color: #c6cde8;
}

.profilePicture > img {
  border-radius: 30px;
}

.socialLinks {
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.logo {
  padding: 5px;
}

@media only screen and (max-width: 450px) {
  .teamprofileContainer {
    display: none;
    justify-content: center;
  }
}
