.roadmap {
  display: block;
  padding: 20px;
}

.roadmapheader {
  justify-content: center;
  color: #c6cde8;
  font-family: "CaviarDreamsBold";
  font-weight: bold;
  font-size: 40px;
  padding-top: 20px;
}

.roadmapContainer {
  width: 60%;
  justify-content: center;
  font-size: small;
  justify-content: flex-end;
  text-size-adjust: inherit;
  display: contents;
}

.q1Container {
  display: flex;
  padding: 30px;
  align-items: center;
  align-self: flex-start;
  justify-content: center;
}

.q2Container {
  display: flex;
  padding: 30px;
  align-items: center;
  align-self: flex-end;
  justify-content: center;
}

.q3Container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
}

.q4Container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
}

.quarterevents {
  border: 5px solid;
  border-radius: 20px;
  border-color: #f2d5e7;
  padding: 20px;
  color: white;
  font-weight: bold;
  background-color: #f2d5e7;
}

.event {
  font-family: "CaviarDreamsBold  ";
  color: white;
  font-size: 24px;
}
